.container{
    width: 450px;
    margin: auto;
    background-color: rgb(168, 204, 212); 
    padding: 11px;
    margin-top: 42px;
    border-radius: 6px;
}
.input{
    text-align: center;
    
}
.btns{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 22px;
}
.container input{
    width: 92%;
    font-size: 22px;
    padding: 9px;
    border: none;
    border-radius: 3px;
   
}
 
.container button{
    width: 100px;
    height: 40px;
    margin: 4px;
    border-radius: 3px;
    font-size: 22px;
    border: none;
    background-color: white;
    cursor: pointer;
}
.container .equalSign{
    background-color: rgb(0, 162, 255);
    color: white;
    font-weight: bold;
}
@media screen and (max-width:500px){
    .container {
        width: 90%;
    }
}